
*{margin: 0;padding: 0;
}
html,body{width: 100%;min-height:100%;
}
body{
  font-family: 'Montserrat', sans-serif;
  font-size: 100%;
  background: url(../../static/img/bg.ea367c0.jpg) center center/100% 100% no-repeat;
}
body:before{
  content: '';
  display: table;
}
#app {
  width: 100%;
  min-height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}

.mr20{margin-right: 20px
}
::-webkit-input-placeholder { /* WebKit browsers */
    color:    #999;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color:    #999;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
    color:    #999;
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
    color:    #999;
}
.clear{clear:both;
}
/* clear float */
.login .top-login{
    width: 130px;
    height: 130px;
    display: block;
    position: relative;
    margin: 0 auto;
    text-align: center;
}
.top-login span{
    border: 2px solid #F36;
    width: 105px;
    height: 105px;
    display: block;
    margin: 0px auto;
    position: absolute;
    top: 11px;
    left: 11px;
    border-radius: 50%;
}
.top-login span img{
  display: block;
  width: 64px;
  height: 64px;
  /* -webkit-transform: rotate(-45deg) translate3d(0, 0, 0);
  -moz-transform: rotate(-45deg) translate3d(0, 0, 0);
  -ms-transform: rotate(-45deg) translate3d(0, 0, 0);
  -o-transform: rotate(-45deg) translate3d(0, 0, 0);
  transform: rotate(-45deg) translate3d(0, 0, 0); */
  margin: 20.5px;
}
.login h1{
  margin: 10px 0;
  color: white;
}
.login-top{
	width: 80%;
  display: block;
  margin: 5px auto;
}
.login-ic {
  background: rgba(255, 255, 255, 0.32); 
	margin-bottom:0.7em;
  padding: 4px 8px; /* Safari */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  border-radius: 10px;
}
.login-ic i {
    background: url(data:image/png;base64,)no-repeat 6px 6px;
    width: 38px;
    height: 38px;
    display: inline-block;
}
.login-ic i.icon {
    background: url(data:image/png;base64,)no-repeat 6px 6px;
}
.login-ic input[type="text"],.login-ic  input[type="password"] {
    background: none;
    outline: none;
    font-size: 18px;
    font-weight: 400;
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-left: 1px solid #fff;
    width: auto;
    display: inline-block;
    margin-left: 7px;
}
.log-bwn {
  width: 100%;
  text-align: center; /* Safari */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack:justify;
      -ms-flex-pack:justify;
          justify-content:space-between;
}
.log-bwn input[type="submit"] {
  font-size: 15px;
  font-weight: 700;
  border-radius: 10px;
  color: #fff;
  padding: 8px 0;
  background: #ff3366;
  display: inline-block;
  -webkit-box-flex:1;
      -ms-flex-positive:1;
          flex-grow:1;
  width: 100%;
	outline:none;
	border:2px solid #ff3366;
	cursor:pointer;
	text-transform:uppercase;
}
.log-bwn input[type="submit"]:hover{
	transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
	border:2px solid #fff;
}
.login p{
  color: white;
}
.login p.copy{
  font-size: 20px;
  margin-top: 20px;
}
.login p.copy a{
  margin-top: 20px;
	color:#fff;
	text-decoration:none;
}
.login p.copy a:hover{
	text-decoration:underline;
}
.login .rutrueInfo{
  color: red;
  font-size: 25px;
}
.login p.des{
  padding: 0 10%;
  text-align: left;
  font-style: italic;
  line-height: 25px;
}

.forum h1{
  color: red;
}
.forum .exit{
  margin-top: 200px;
  padding: 4px 15px;
  font-size: 20px;
  border-radius: 20px;
  background-color: #fff;
  color: black;
}
